.product{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:flex-end;
    width: 100%;
    padding:20px;
    margin: 10px;
/*    
          
*/
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
}
.product > img{
    max-height: 200px;
/*if overlapping persists in product, use calc(100%-10px)
also, implement box-sizing of border-box
*/
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}
.product > button{
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 ;
/*     border-color: #a88734  #9c7e31 #846a29; */
    color: #111;
}
.product__info{
    /*width: 100%;*/
    height: 100px;
    margin-bottom: 15px;
}
.product__price{
    margin-top: 5px;
}
.product__rating{
    display: flex;
}
